<template>
  <div class="DeptListView">
    <h1>请选择科室</h1>
    <el-row justify="center">
      <el-col class="DeptListCollapse">
        <el-collapse
          v-for="(item, index) in deptList"
          :key="index"
          v-model="activeName"
          accordion
        >
          <el-collapse-item :title="item.deptName">
            <el-table
              :data="item.deptList"
              :show-header="false"
              class="DeptTable"
              @row-click="ToDeptSchForDoc"
            >
              <el-table-column
                label="科室名称"
                prop="deptName"
              ></el-table-column>
            </el-table>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Store } from "@/stores";
import { API, Config } from "@/utils/API";
import { ElMessage } from "element-plus";
import { getPhsSign } from "@/utils/Func";
import { ref } from "vue";

const store = Store();

export default {
  name: "DeptListView",
  data() {
    return {
      deptList: [],
      activeName: ref("1"),
    };
  },
  methods: {
    getDeptList() {
      const headers = {
        phsSign: getPhsSign(),
        phsId: Config.phsid,
        "Content-Type": "application/json",
      };
      console.log(headers);
      const data = {
        args: {
          clinicalType: "1",
          hosId: store.HospitalZone.hosId,
          sysCode: store.HospitalZone.sysCode,
        },
        token: store.token,
      };
      this.axios.post(API.get_deptlist, data, { headers }).then((res) => {
        if (res.data.code === "0") {
          this.deptList = res.data.result.dataList;
        } else {
          ElMessage.error(res.data.message);
        }
      });
    },
    ToDeptSchForDoc(item) {
      console.log(item);
      store.Dept = item;
      this.$router.push("/deptschfordoc");
    },
  },
  mounted() {
    if (!store.token) {
      this.$router.push("/login");
    } else {
      this.getDeptList();
    }
  },
};
</script>

<style scoped>
.DeptListView {
  text-align: center;
  align-items: center;
}

.DeptListCollapse {
  max-width: 400px;
}
</style>
